<template>
  <v-tab-item>
    <v-row class="mt-3">
      <v-col 
         v-if="isLoading"
        cols="12" sm="4">
        <Loader type="card" />
      </v-col>
      <v-col 
        v-else
        cols="12" sm="4"
        v-for="(item, i) in dataProduct" :key="i">
        <v-img 
          :src="`http://progresio.id/api/public/uploads/product/${item.gambar}`" 
          max-width="100%" 
          max-height="150"
          class="img-container">
          <div class="overlay-img">
            <Edit :data="item" />
            <Hapus :id="item.id" />
          </div>
        </v-img>
        <div class="title black--text">{{ item.nama }}</div>
        <div class="subtitle-1">{{ item.deskripsi }}</div>
      </v-col>
    </v-row>

    <Tambah/>
  </v-tab-item>
</template>

<style scoped>
  .overlay-img{
    display: flex;
    width: 100%;
    height: 100%;
    justify-content: center;
    align-items: center;
    background-color: rgba(0,0,0,0.3);
    visibility: hidden;
    transition: visibility 0.2s ease;    
  }
  .img-container:hover .overlay-img{
    visibility: visible;
  }
</style>

<script>
  import { mapState } from 'vuex'
  import Tambah from './Product/Tambah'
  import Loader from '@/components/Loader'
  import Edit from './Product/Edit'
  import Hapus from './Product/Hapus'

  export default{
    components: {
      Tambah,
      Loader,
      Edit,
      Hapus
    },

    created() {
      this.$store.dispatch('dataStartupProduct/getDataProduct')
      this.$store.dispatch('dataStartupProduct/updateIsLoading', true)
    },

    computed: {
      ...mapState('dataStartupProduct', ['dataProduct', 'isLoading'])
    }
  }
</script>
