<template>
	<v-dialog
    v-model="dialog"
    width="500"
    persistent>
    <template v-slot:activator="{ on }">
			<v-btn
				v-on="on"
        icon
        class="white mr-3"
        color="primary">
        <v-icon small>mdi-pencil</v-icon>
      </v-btn>
    </template>

    <v-card>
      <div class="pa-2 text-center">
        <span class="title">Edit Founder</span>
      </div>
      <v-form v-model="valid" ref="form" enctype="multipart/form-data">
        <v-card-text>
          <v-text-field
            v-model="data.nama"
            label="Name"
            dense
            :rules="requiredRule"
            autocomplete="off"
          ></v-text-field>
          <v-text-field
            v-model="data.posisi"
            label="Posisi"
            dense
            :rules="requiredRule"
            autocomplete="off"
          ></v-text-field>
          <v-file-input
            accept="image/*"
            v-model="gambar"
            show-size
            counter
            label="Documentation"
          ></v-file-input>         

          <Notif :msg="msg" :status="status" @visible="visible" />
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="primary"
            text
            @click="dialog = false">
            kembali
          </v-btn>
          <v-btn
            :disabled="!valid"
            :loading="isLoading"
            color="primary"
            @click.prevent="validate"
            type="submit"
            depressed>
            simpan
          </v-btn>
        </v-card-actions>
      </v-form>
    </v-card>
  </v-dialog>
</template>

<script>
  import { mapState } from 'vuex'
  import Notif from '@/components/Notif'

  export default {
    props: ['data'],

    data () {
      return {
        dialog: false,
        valid: true,
        modal: false,
        gambar: null,
        requiredRule: [v => !!v || 'Data harus diisi'],
        msg: {
          success: 'Berhasil diubah',
          error: 'Gagal diubah',
          visible: false
        }
      }
    },

    components: {
      Notif
    },

    computed: {
      ...mapState('dataStartupFounder', ['isLoading', 'status']),      
    },

    watch: {
      status(val) {
        if (val === null) this.dialog = false
      }
    }, 

    methods: {
      validate () {
        if (this.$refs.form.validate()) {
          let data = new FormData()
          data.append('id', this.data.id)
          data.append('nama', this.data.nama)
          data.append('posisi', this.data.posisi)
          if (this.gambar !== null) data.append('gambar', this.gambar)
          
          this.$store.dispatch('dataStartupFounder/putDataFounder', data)
          this.$store.dispatch('dataStartupFounder/updateIsLoading', true)
          this.msg.visible = true
        }
      },
      visible(val) {
        this.msg.visible = val
        this.$store.commit('dataStartupFounder/updateStatus', null)
      }
    }
  }
</script>